import React from 'react';
import styled from 'styled-components'
import { base } from 'grommet'
const Square = styled.div`
  width: 100%;
  padding-bottom: 100%;
  position: relative;
  display: flex;
  ${props => props.background ? `background-color: ${props.background}` : ''}
`

const index = ({ index, data, Template, direction, onSquarePressed, colorLookup}) => {

    return (
        <Square 
            onClick={() => onSquarePressed && onSquarePressed(data)} 
            background={colorLookup && colorLookup[direction]}
        >
            <Template direction={direction} {...data} />
        </Square>
    );
}

export default index;